import { imageShowUrl } from "../../../../services/BaseUrl";
import moment from "moment";
import style from "./Activity.module.css";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../optimized/Ui/Button/CreateButton";
const SubjectTeacherFeedback = ({ activity }) => {
  const params = useParams();
  const navigate = useNavigate();
  const { t } = useTranslation();

  const onNavigate = () => {
    navigate(`/q/${params.username}/notifications?a=feedback`, {
      state: {
        ifid: activity?.student_feedback,
        staffId: activity?.staffId,
        sid: activity?.notifyPublisher,
        notifyId: activity?._id,
      },
    });
  };
  return (
    <div
      className={
        activity?.notifyReadStatus === "Unread"
          ? `${style.item} ${style.item_active}`
          : style.item
      }
    >
      <div className={style.eachItem}>
        <div className={style.eachItemleft}>
          <img
            src={
              activity?.notifyByInsPhoto?.insProfilePhoto
                ? `${imageShowUrl}/${activity?.notifyByInsPhoto?.insProfilePhoto}`
                : "/images/ins_default_profile.svg"
            }
            alt="student avatar"
          />
          <div>
            <p>{activity?.notifyContent}</p>

            {activity?.student_feedback_status === "Submitted" ? (
              <CreateButton
                label={t("submitted")}
                customStyle={{
                  margin: "0.2rem 0 0 0",
                  padding: "0.1rem 0.7rem",
                  width: "fit-content",
                }}
                onAction={() => {}}
              />
            ) : (
              <CreateButton
                label={t("give_feedback")}
                customStyle={{
                  margin: "0.2rem 0 0 0",
                  padding: "0.1rem 0.7rem",
                  width: "fit-content",
                }}
                onAction={onNavigate}
              />
            )}
          </div>
        </div>
        <div className={style.text}>
          <p>{moment(activity?.notifyTime).format("LT")}</p>
        </div>
      </div>
    </div>
  );
};

export default SubjectTeacherFeedback;
